.user-profile__wrapper {
  color: white;
  background-color: var(--accent-dark);
  height: 100%;
  h3,
  h4 {
    margin-bottom: 8px;
  }

  & > div:nth-child(1) {
    display: grid;
    place-items: center;
    text-transform: capitalize;
    margin-bottom: 20px;

    img {
      // border-radius: 50%;
      // margin: 20px;
      width: 100%;
      // margin-bottom: 12px;
      // height: 120px;
      // width: 120px;
      object-fit: cover;
    }

    .__edit-button {
      background-color: var(--accent-darker);
      // background-color: var(--secondary-dark);
      display: grid;
      place-items: center;
      padding: 12px;
      width: calc(100% + 20px);
      position: relative;
      left: -10px;
      margin-bottom: 12px;

      width: 100%;
      left: 0px;
    }

    .__name {
      padding-left: 40px;
      // font-size: 1.8em;
      // color: white;
      font-size: 2.4em;
      padding-right: 50px;
      font-weight: normal;
      line-height: 1em;
      color: white;
      // width: calc(100% - 80px);
    }

    .__type {
      position: relative;
      // border: solid;
      margin-top: 8px;
      width: calc(100% - 80px);
      opacity: 0.8;

      // left: ;
    }
  }

  & > div:nth-child(2) {
    background-color: var(--accent-dark);
    padding: 8px;
    padding-left: 40px;
    padding-right: 40px;
    margin-bottom: 12px;
    .__skill-chip {
      background-color: #ffffff;
      border-radius: 9px;
      color: var(--accent-dark);
      font-weight: normal;
      font-size: 16px;
      padding: 2px 12px;
      margin-right: 4px;
      margin-top: 12px;
    }
  }

  .__contact-info-wrapper {
    background-color: var(--accent-dark);
    padding: 8px;
    padding-left: 40px;

    h3,
    h4 {
      margin-bottom: 12px;
    }

    .lni {
      margin-right: 4px;
      opacity: 0.6;
    }

    div {
      margin-bottom: 4px;
    }
  }
}
